.form {
 max-width: 575px;
 &__title {
  font-size: 24px;
  @include bold;
  line-height: 35px;
  margin-bottom: 45px; }
 &__subtitle {
  display: block;
  margin-bottom: 50px; }
 &__input {
  width: 270px;
  margin-bottom: 30px;
  &:first-child {
   margin-right: 30px; } }
 &__button {
  padding: 28px;
  display: block;
  width: 100%;
  font-size: 16px; }
 &__footer {
  display: flex; }
 &__contact {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: $text-color;
  font-family: 'Museo Sans Light';
  margin-top: 27px; }

 &__call {
  margin-left: 140px;
  width: 191px;
  margin-top: 10px;
  color: $text-color;
  font-family: 'Museo Sans Light';
  font-size: 15px;
  &::before {
    display: block;
    content: url(../img/feedback/phone.png);
    margin-left: -60px;
    margin-top: 22px;
    margin-bottom: -46px; } }

 &__number {
  padding-top: 10px;
  @include bold; } }

